import { ITimeLinesSolicitacaoListaView, ITimeLinesSolicitacaoListaController } from "../../contracts/Solicitacao/TimeLinesSolicitacaoListaContract";
import axios from "../../plugins/axios"

import { Storage } from "../../configuration/storage"
import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

export class TimeLinesSolicitacaoListaController implements ITimeLinesSolicitacaoListaController {

    _view: ITimeLinesSolicitacaoListaView

    constructor(view: ITimeLinesSolicitacaoListaView) {
        this._view = view
    }

    carregarItens(): void {
        this._view.loading(true)

        axios.get('solicitacao/solicitacoesEmAprovacao/' + Storage.get("User")).then((response: any) => {

           //
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }


}